import * as events from '../../../src/event-helper';
import {isJsonLdScriptTag} from '../../../src/dom';
import {toArray} from '../../../src/core/types/array';
import {tryParseJson} from '../../../src/core/types/object/json';

const rules = [
  // if it says it's a webview, let's go with that
  'WebView',
  // iOS webview will be the same as safari but missing "Safari"
  '(iPhone|iPod|iPad)(?!.*Safari)',
  // Android Lollipop and Above: webview will be the same as native but it will
  // contain "wv" Android KitKat to lollipop webview will put {version}.0.0.0
  'Android.*(wv|.0.0.0)',
  // old chrome android webview agent
  'Linux; U; Android',
];

const webviewRegExp = new RegExp('(' + rules.join('|') + ')', 'ig');

const ampPixelURL = 'https://apester.go2cloud.org/aff_i';

/**
 * Returns if ua belongs to a webview
 *
 * @param {string} ua
 * @return {boolean}
 */
function isWebview(ua) {
  return !!ua.match(webviewRegExp);
}

/**
 * @description
 * regex for detecting mobile devices.
 * (including blackberry and other rare mobile devices).
 * @return {boolean}
 */
function isMobileDevice() {
  const userAgent = navigator.userAgent || navigator.vendor || window.opera;
  return (
    /(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino|android|ipad|playbook|silk/i.test(
      userAgent
    ) ||
    /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(
      userAgent.substr(0, 4)
    )
  );
}

/**
 * Gets the user platform.
 * @return {string}
 */
export function getPlatform() {
  const webview = isWebview(navigator.userAgent);
  const isMobile = isMobileDevice();
  return isMobile ? `mobile${webview ? '-webview' : ''}` : 'desktop';
}

/**
 * Extracts tags from a given element .
 * @param {!Node} element
 * @return {*} TODO(#23582): Specify return type
 */
export function extractElementTags(element) {
  const tagsAttribute = element && element.getAttribute('data-apester-tags');
  if (tagsAttribute) {
    return tagsAttribute.split(',').map((tag) => tag.trim()) || [];
  }
  return [];
}

/**
 * Extract article's first 5 words from the title and use them for tags.
 * @param {!Document|!ShadowRoot} root
 * @return {!Array<string>}
 */
export function extractTitle(root) {
  const scriptTags = toArray(
    root.querySelectorAll('script[type="application/ld+json"]')
  );
  return scriptTags
    .map((scriptTag) => {
      if (!scriptTag || !isJsonLdScriptTag(scriptTag)) {
        return {};
      }
      return tryParseJson(scriptTag.textContent) || {};
    })
    .map((jsonLd) => jsonLd && jsonLd['headline'])
    .filter((e) => typeof e === 'string')
    .map((title) =>
      title
        .trim()
        .split(' ')
        .filter((w) => w.length > 2)
    )
    .reduce((result, headline) => {
      return result.concat(headline);
    }, [])
    .slice(0, 5);
}
/**
 * Extracts article meta keywords
 * @param {!../../../src/service/ampdoc-impl.AmpDoc} ampdoc
 * @return {Array<string>}
 */
export function extractArticleTags(ampdoc) {
  return (ampdoc.getMetaByName('keywords') || '')
    .split(',')
    .map((e) => e.trim())
    .filter(Boolean);
}

/**
 * Extracts tags from a given element and document.
 * @param {!../../../src/service/ampdoc-impl.AmpDoc} ampdoc
 * @param {!Node} element
 * @return {Array<string>}
 */
export function extractTags(ampdoc, element) {
  const extractedTags = extractElementTags(element) || [];
  const articleMetaTags = extractArticleTags(ampdoc);
  const concatedTags = extractedTags.concat(
    articleMetaTags.length
      ? articleMetaTags
      : extractTitle(ampdoc.getRootNode()) || []
  );
  const loweredCase = concatedTags.map((tag) => tag.toLowerCase().trim());
  const noDuplication = loweredCase.filter(
    (item, pos, self) => self.indexOf(item) === pos
  );
  return noDuplication;
}

/**
 * Adds fullscreen class to an element
 * @param {!Element} element
 */
export function setFullscreenOn(element) {
  element.classList.add('amp-apester-fullscreen');
}

/**
 * removes fullscreen class from an element
 * @param {!Element} element
 */
export function setFullscreenOff(element) {
  element.classList.remove('amp-apester-fullscreen');
}

/**
 * Registers to an event
 * @param {string} eventName
 * @param {!Function} callback
 * @param {!EventTarget} win
 * @param {!Element} iframe
 * @param {!Array} unlisteners
 */
export function registerEvent(eventName, callback, win, iframe, unlisteners) {
  const unlisten = events.listen(win, 'message', (event) => {
    const fromApesterMedia = iframe.contentWindow === event.source;
    if (events.getData(event)['type'] === eventName && fromApesterMedia) {
      callback(events.getData(event));
    }
  });
  unlisteners.push(unlisten);
}

/**
 * Generates a pixel url
 * @param {string} publisherId
 * @param {string} affiliateId
 * @return {string}
 */
export function generatePixelURL(publisherId, affiliateId) {
  const qsObj = {
    'offer_id': 2,
    'aff_id': affiliateId,
    'aff_sub': publisherId,
    'aff_sub2': window.location.hostname,
    'aff_sub3': 'amp',
  };

  const qs = Object.keys(qsObj)
    .map((key) => `${key}=${qsObj[key]}`)
    .join('&');

  return `${ampPixelURL}?${qs}`;
}
